* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

h1 {
    text-align: center;
    padding-top: 2rem;
    width: 100vw;
}

body {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.main-container {
    flex: 1;

    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 90rem;
}

.test-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex: 3;
}

.test-container p {
    font-size: 6rem;
}


#length-container {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap: .3rem;
}

.checkboxes {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-self: center;
    align-items: flex-start;
    gap: .3rem;
}

button {
    padding: .2rem .5rem;
    min-width: 2rem;
    min-height: 2rem;
}

.footer-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap: 1rem;
    margin: 1rem 0;
}

#counter {
    font-size: 1.2rem;
}